import Vue from 'vue'
import App from './App.vue'
import axios from 'axios'
import VueAxios from "vue-axios"
import VueRouter from "vue-router"
import ViewUI from "view-design"
import 'view-design/dist/styles/iview.css'

import 'vue-beauty/package/style/vue-beauty.min.css'
import Vant from 'vant'
import 'vant/lib/index.css'

// ui框架
import vueBeauty from 'vue-beauty'



Vue.config.productionTip = false
Vue.config.devtools = true

Vue.use(vueBeauty)
//网络请求框架
Vue.use(VueAxios,axios)
//路由
Vue.use(VueRouter)
//iview组件初始化
Vue.use(ViewUI)
// vant组件库
Vue.use(Vant)


import Login from './components/user/Login'
import SchoolSetting from './components/school/setting'
import SchoolManager from './components/school/manager'
import SchoolGallery from './components/school/gallery'
import SchoolVideo from './components/school/video'
import SchoolMember from './components/school/member'
import SchoolStudying from './components/school/studying'
import ClassManager from './components/school/classmanager'
import Home from './components/Home'
const router = new VueRouter({
  routes: [
    {path:'/login',name: '/login', component: Login},
    {path:'/Home',name:'/Home',component: Home,
    children: [{
      path:"school/manager",
      component: SchoolManager
    },{
      path:"school/gallery",
      component: SchoolGallery
    },{
      path:"school/video",
      component: SchoolVideo
    },{
      path:"school/setting",
      component: SchoolSetting
    },{
      path:"school/member",
      component: SchoolMember
    },{
      path:"school/studying",
      component: SchoolStudying
    },{
      path:"school/classmanager",
      component: ClassManager
    }]}
  ]
})


new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
